import React from 'react';
import styled from 'styled-components';

export default function Timeline () {

    // data
    const data = [
        {
            id: 0,
            year: 2010,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        },
        {
            id: 1,
            year: 2010,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        },
        {
            id: 2,
            year: 2011,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        },
        {
            id: 3,
            year: 2012,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        }
    ]

    const Container = styled.div`
        background-color: grey;
        display: flex;
        height: 600px;
        width: 100%;
        position: relative;
        justify-content: center;
    `;

    const Column = styled.div`
        height: 100%;
        width: 75px;
        background-color: red;
        border-right: 1px dashed rgba(0,0,0,.2);
        position: relative;

        &:hover ${VerticalLine} {
            border: 1px solid black;
        }
    `;

    const LineMiddle = styled.div`
        width: 100%;
        top: 50%;
        position: absolute;
        border-bottom: 1px solid black;

    `;

    const Year = styled.span`
        font-size: 24px;
        position: absolute;
        top: 50%;
        margin: 0;
        font-size
    `;

    const Card = styled.div`
        width: 225px;
        background-color:white;
        position:absolute;
        z-index: 2;
        padding: 10px;
        ${propsS => propsS.bottom  ? 'bottom:' + propsS.bottom + '%;' : ''}
        ${propsS => propsS.top ? 'top:' + propsS.top + '%;' : ''}

        h5 {
            margin: 0 0 10px 0;
        }

        p {
            margin: 0;
        }
    `

    const CardLine = styled.div``
    const VerticalLine = styled.div`
        margin: 0;
        position:relative;
        top: 50%;
        left: -1px;
        width: 2px;
        height: 5px;
        background-image: linear-gradient(to bottom, rgb(1, 58, 81), 26%, rgb(0, 229, 158));
        height: ${propsS => propsS.height || 0}%;

        ${propsS => propsS.translateY ? `
            background-image: linear-gradient(to top, rgb(1, 58, 81), 26%, rgb(0, 229, 158));
            transform: translateY(-100%);
        ` : ''}
    `;
        
    // background-image: linear-gradient(to top, rgb(1, 58, 81), 26%, rgb(0, 229, 158));
    function getColumn() {

        const positions = [
            { bottom:  75},
            { bottom: 55},
            { top: 75},
            { top: 55},
        ]
        const years = {};
        return data.map((d, index)=>{
            const pos = positions[index % 4];
            const year = years[d.year] ? '' : d.year;
            years[d.year] = true;

            let height = (pos.bottom || pos.top) - 50;
            let translateY = pos.bottom ? true: false;
            return (
            <Column key={d.id}>
                <Card top={pos.top} bottom={pos.bottom}>
                    <h5>{d.title}</h5>
                    <p>{d.text}</p>
                </Card>
                <Year> {year} </Year>

                <VerticalLine height={height} translateY={translateY} />
            </Column>
            );
        });
    }

    return (
        <Container>
            {getColumn()}
            <LineMiddle />
        </Container>
    );
}